import * as React from "react";
import { Dialog, DialogContent } from "@mui/material";
import { ModalDialogprops } from "../../types/types";
import CloseIcon from "@mui/icons-material/Close";
import { useAppDispatch } from "../../assets/hooks";
import { closePopup } from "../../store/Slices/popupSlice";

const CustomModal = ({ open, children, additionalData }: ModalDialogprops<{ width: any, height?: any, show?: any, close?: any }>) => {
  const dispatch = useAppDispatch();

  return (
    <>
      <Dialog
        open={open}
        onClose={() => additionalData?.close === "none" ? console.log("") : dispatch(closePopup())}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        PaperProps={{
          sx: {
            minWidth: open ? "200px" : "0px", 
            minHeight: open ? "100px" : "0px",
            height: open ? `${additionalData?.height}` : "0px",
            width: open ? { xs: "100%", md: `${additionalData?.width}` } : "0px",
            padding: open ? "0" : "0px",
            maxWidth: open ? "1300px" : "0px",
            maxHeight: open ? "1300px" : "0px",
            position:"relative",
            zIndex:1300
            // transition: "all 0.3s ease-in-out", 
          },
        }}
      >
        {additionalData?.show === "none" ? "" :
          <CloseIcon
            onClick={() =>{console.log("Clicked");dispatch(closePopup())}}
            sx={{
              position: "absolute",
              right: 14,
              top: 14,
              zIndex:1,
              color: "#E94040",
              cursor: "pointer",
              marginRight: "0px",
              "&:hover": {
                backgroundColor: "#E94040",
                color: "#fff",
              }
            }}
          />}
        <DialogContent
          sx={{
            overflowY: "scroll",
            "&::-webkit-scrollbar": {
              width: "12px",
            },
            "&::-webkit-scrollbar-thumb": {
              backgroundColor: "#fff",
              borderRadius: "8px",
            },
            overflow: "visible",
            padding: { xs: 0, sm: "0" }
          }}
        >
          {children}
        </DialogContent>
      </Dialog>
    </>
  );
};

export default CustomModal;
